import { Box, Card, CardMedia, Divider, TextField, Typography, MenuItem, Select, FormControl, InputLabel, Button, InputAdornment } from "@mui/material";
import React from "react";
import logo from '../logo.svg';
import Result from "./Result";
import { TicketCalculator } from "./TicketCalculator";
import { TicketCalculatorController } from "./TicketCalculatorController";

export class TicketCalculatorView extends React.Component {
    #controller

    constructor(props) {
        super(props);
        this.state = {
            success: false
        };
        this.#controller = new TicketCalculatorController(new TicketCalculator(), this);
    }

    render() {
        return (
            <Box sx={{ minWidth:1000, backgroundColor: 'blue', p:5, borderRadius: 10}} onChange={() => console.log(this.state.model)}>
                <Box component='form' sx={{ display: 'grid', gridTemplateRows: 'repeat(3, 1fr)', p:5, backgroundColor: 'white'}}>
                    <Typography variant='h4' component='h4' color="Purple">Bảng Tính</Typography>
                    <Box component='form' sx={{ display: 'grid', m:1, gridTemplateColumns: 'repeat(2, 1fr)', columnGap:2, width:'100%'}}>
                        <TextField require='true' label="Coordinator" onChange={(e) => this.state.model.coordinator = e.target.value}/>
                        <TextField require='true' label="Church" onChange={(e) => this.state.model.church = e.target.value}/>
                    </Box>
                    <Box component='form' sx={{ display: 'flex', m:1, columnGap:1, width:'100%'}}>
                        <Typography sx={{ alignSelf:'center', textAlign:'left', mr: 2 }} variant='h6' component='h6' color="Purple">Ngày Lễ</Typography>
                        <TextField sx={{ width:'30%' }} require='true' label="Ngày" type='number'
                                onChange={(e) => this.state.model.massDay = e.target.value}/>
                        <FormControl sx={{ flexGrow:1 }}>
                            <InputLabel>Tháng</InputLabel>
                            <Select
                                label="Tháng"
                                onChange={this.#controller.changeMassMonth}
                            >
                                <MenuItem value={"June"}>June</MenuItem>
                                <MenuItem value={"July"}>July</MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Box component='form' sx={{ display: 'flex', m:1, columnGap:1, width:'100%'}}>
                        <Typography sx={{ alignSelf:'center', textAlign:'left', mr: 2 }} variant='h6' component='h6' color="Purple">Giờ Lễ</Typography>
                        <TextField sx={{ width:'30%' }} require='true' label="Giờ" type='number'
                                onChange={(e) => this.state.model.massHour = e.target.value}/>
                        <FormControl sx={{ flexGrow:1 }}>
                            <InputLabel>Phút</InputLabel>
                            <Select
                                label="Phút"
                                onChange={this.#controller.changeMassMinute}
                            >
                                <MenuItem value={0}>00</MenuItem>
                                <MenuItem value={15}>15</MenuItem>
                                <MenuItem value={30}>30</MenuItem>
                                <MenuItem value={45}>45</MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Divider flexItem />
                    <Box component='form' sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', m:1, gap:2, width:'100%'}}>
                        <TextField require='true' label="Số vé trước khi bán" type='number' InputProps={{
                            endAdornment: <InputAdornment position="end"><strong>vé</strong></InputAdornment>,
                        }}/>
                        <TextField require='true' label="Số vé sau khi bán" type='number' InputProps={{
                            endAdornment: <InputAdornment position="end"><strong>vé</strong></InputAdornment>,
                        }}/>
                        <TextField require='true' label="Số tiền trước khi bán" type='number' InputProps={{
                            startAdornment: <InputAdornment position="start"><strong>$</strong></InputAdornment>,
                        }}/>
                        <TextField require='true' label="Số tiền sau khi bán" type='number' InputProps={{
                            startAdornment: <InputAdornment position="start"><strong>$</strong></InputAdornment>,
                        }}/>
                    </Box>
                    <Box sx={{ textAlign:'right', width:'100%' }}>
                        <Button sx={
                            [{ backgroundColor:'purple', color:'white', border:1, px:2 }, 
                            {'&:hover': {backgroundColor:'white', color:'purple', border:1, borderColor:'purple'}}
                            ]}
                            onClick={() => this.#controller.showAll()}
                        >Xuất Kết Quả</Button>
                    </Box>
                </Box>
                <Divider orientation="vertical" flexItem />
                {
                    this.state.success ?
                    <Result result={this.state.result}
                        closeResult={()=>this.setState({result: false})}
                        info={this.#controller.info}
                        input={this.#controller.input}
                        calculated={{
                            sold: this.state.sold,
                            profit: this.state.profit,
                            donation: this.state.donation,
                        }}
                        image={(imageURL) => this.setState({image: imageURL})}
                    /> : <></>
                }
            </Box>
        );
    }
}