import { MenuItem } from "@mui/material";

export class TicketCalculatorController {
    #model
    #view

    constructor(model, view) {
        this.#model = model;
        this.#view = view;
    }

    changeMassHour = (event) => { this.#model.massHour = event.target.value; }
    changeMassMonth = (event) => { this.#model.massMonth = event.target.value; }
    changeMassMinute = (event) => { this.#model.massMinute = event.target.value; }

    get info() {
        return {
            coordinator: this.#model.coordinator,
            location: this.#model.church,
            date: this.#model.date,
            time: this.#model.time
        }
    }
    get input() {
        return {
            beginningChange: this.#model.cashBeforeSold,
            totalAmount: this.#model.cashAfterSold,
            totalTickets: this.#model.ticketBeforeSold,
            leftTickets: this.#model.ticketsAfterSold,
        };
    }

    showAll() {
        console.log(this.info);
    }
}