import {collection, doc, setDoc} from "firebase/firestore";
import db from '../Firebase';

export class TicketCalculator {
    #church
    #coordinator
    #massDay
    #massMonth
    #massHour
    #massMinute
    #ticketsBeforeSold
    #ticketsAfterSold
    #cashBeforeSold
    #cashAfterSold

    constructor() {
        this.#church = '';
        this.#coordinator = '';
        this.#massDay = 1;
        this.#massMonth = 'June';
        this.#massHour = 9;
        this.#massMinute = 30;
        this.#ticketsBeforeSold = 0;
        this.#ticketsAfterSold = 0;
        this.#cashBeforeSold = 0;
        this.#cashAfterSold = 0;
    }

    get church() { return this.#church; }
    get coordinator() { return this.#coordinator; }
    get date() { return this.#massMonth + " " + this.#massDay; }
    get time() { return this.#massHour + ":" + this.#massMinute; }
    get massMonth() { return this.#massMonth; }
    get massDay() { return this.#massDay; }
    get massHour() { return this.#massHour; }
    get massMinute() { return this.#massMinute; }
    get ticketsBeforeSold() { return this.#ticketsBeforeSold; }
    get ticketsAfterSold() { return this.#ticketsAfterSold; }
    get cashBeforeSold() { return this.#cashBeforeSold; }
    get cashAfterSold() { return this.#cashAfterSold; }
    
    set church(church) { this.#church = church; }
    set coordinator(coordinator) { this.#coordinator = coordinator; }
    set massMonth(month) { this.#massMonth = month; }
    set massDay(day) { this.#massDay = day; }
    set massHour(hour) { this.#massHour = hour; }
    set massMinute(minute) { this.#massMinute = minute; }
    set ticketsBeforeSold(amount) { this.#ticketsBeforeSold = amount; }
    set ticketsAfterSold(amount) { this.#ticketsAfterSold = amount; }
    set cashBeforeSold(amount) { this.#cashBeforeSold = amount; }
    set cashAfterSold(amount) { this.#cashAfterSold = amount; }

    calculateSoldTickets() { return this.#ticketsBeforeSold - this.#ticketsAfterSold; }
    calculateProfit() { return this.calculateSoldTickets() * 25; }
    calculateDonation() { return this.#cashAfterSold - this.#cashBeforeSold - this.calculateProfit()}

    exportRecordToFirestore() {
        const ticketRecord = {
            church: this.#church,
            coordinator: this.#coordinator,
            massDay: this.#massDay,
            massMonth: this.#massMonth,
            massHour: this.#massHour,
            massMinute: this.#massMinute,
            ticketsBeforeSold: this.#ticketsBeforeSold,
            ticketsAfterSold: this.#ticketsAfterSold,
            soldTickets: this.calculateSoldTickets(),
            cashBeforeSold: this.#cashBeforeSold,
            cashAfterSold: this.#cashAfterSold,
            profit: this.calculateProfit(),
            donation: this.calculateDonation()
        };
        const result = doc(collection(db, 'ticket-results'));
        setDoc(result, ticketRecord);
    }
}